@import "~react-image-gallery/styles/css/image-gallery.css";
@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,300&family=Roboto:wght@100&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  /* Firefox */
  /* scrollbar-width: 2px;  */
  /* Internet Explorer and Microsoft Edge */
  /* -ms-overflow-style: inherit;  */
  scrollbar-color: Gray;
  background-color: #F7F7F7;
}

body::-webkit-scrollbar {
  width: 5px;
  /* Width of the scrollbar */
  background-color: #555555;
}

body::-webkit-scrollbar-thumb {
  background-color: #acacac;
  /* Color of the scrollbar thumb */
  border-radius: 5px;
  /* Rounded corners of the scrollbar thumb */
}

body::-webkit-scrollbar-thumb:hover {
  background-color: #b4b4b4;
  /* Color on hover */
}

/* Tạo scrollbar overlay trên nội dung */
body::-webkit-scrollbar-thumb:vertical {
  width: 10px;
  /* Đảm bảo scrollbar vertical hoạt động đúng */
  height: auto;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
}

.spinner {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  align-items: center;
}

.loadingItem {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.top-btn {
  cursor: pointer;
  background: #ffffff00;
  width: 50px;
  height: 50px;
  padding: 15px;
  border-radius: 5px;
  color: black;
  position: fixed;
  bottom: 50px;
  z-index: 100;
  right: 50%;
  transform: translateX(50%);
}

.top-btn--icon {
  animation: gototop 1s linear infinite reverse;
}

@keyframes gototop {
  0% {
    transform: translateY(-0.3rem);
  }

  100% {
    transform: translateY(0.3rem);
  }
}

.ctf button {
  background-color: #ffffff46 !important;
  box-shadow: unset;
}

input {
  touch-action: manipulation;
}